@import ../../sass/index

.button
  padding: 0
  margin: 0
  border: none
  border-radius: 5px
  box-sizing: border-box
  background: transparent
  opacity: 1
  cursor: pointer
  transition: $app-transition
  -webkit-tap-highlight-color: transparent
  &:hover
    opacity: 0.7
  &__menu
    display: none
    width: 25px
    height: 20px
    position: relative
    &_span
      position: absolute
      top: 9px
      left: 0
      width: 100%
      height: 2px
      background: black
      border-radius: 1px
      pointer-events: none
      transition: $app-transition
    &::before, &::after
      content: ''
      position: absolute
      left: 0
      width: 100%
      height: 2px
      border-radius: 1px
      background: black
      transition: $app-transition
    &::before
      top: 0px
    &::after
      bottom: 0px
    @media screen and (max-width: 1000px)
      display: flex
    &.active .button__menu_span
      opacity: 0
    &.active:before 
      top: 9px
      transform: rotate(-45deg)
    &.active:after
      bottom: 9px
      transform: rotate(45deg) 
  &__main
    width: 100%
    height: 40px
    color: #ffffff
    font-size: 24px
    font-weight: 700
    background-image: linear-gradient(30deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%)

.border
  border-radius: 5px
  border: 1px solid $notification-color
.transparent
  background: transparent
  